@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@300;600&family=Ubuntu:wght@400;500;700&display=swap);
:root {
  /* Primary */
  --Light-red: hsl(356, 100%, 66%); /* (CTA text) */
  --Very-light-red-hover: hsl(355, 100%, 74%); /* (CTA hover background) */
  --Very-dark-blue: hsl(208, 49%, 24%); /* (headings) */
  /* Neutral */
  --White: hsl(0, 0%, 100%); /* (text) */
  --White-light: hsla(0, 0%, 100%, 0.7);
  --Grayish-blue: hsl(240, 2%, 79%); /* (footer text) */
  --Very-dark-grayish-blue: hsl(207, 13%, 34%); /* (body copy) */
  --Very-dark-black-blue: hsl(240, 10%, 16%); /* (footer background) */

  /* Gradient */
  /* Background gradient - Intro/CTA mobile nav: */
  --Very-light-red: hsl(13, 100%, 72%);
  --Light-red: hsl(353, 100%, 62%);

  /* Background gradient - body: */
  --Very-dark-gray-blue: hsl(237, 17%, 21%);
  --Very-dark-desaturated-blue: hsl(237, 23%, 32%);
}
* {
  margin: 0;
}

body {
  /* - Family: [Overpass]
- Weights: 300, 600

- Family: [Ubuntu]
- Weights: 400, 500, 700 */

  /* Body Copy */
  font-size: 16px;
  font-family: "Overpass", sans-serif;
  /* font-family: "Ubuntu", sans-serif; */
}

